/**
 * 
 */

const valid = {}

valid.validatePhoneNumber = function (value) {
  if (!value || value === '') {
    return ''
  }
  var phonrReg = /^1\d{10}$/
  if (!phonrReg.test(value)) {
    return ''
  } else {
    return ''
  }
}

export default valid



